import React from 'react';
import { unified } from 'unified';
import remarkParse from 'remark-parse';
import remarkFrontmatter from 'remark-frontmatter';
import remarkHtml from 'remark-html';

import yaml from 'js-yaml';

interface WorkProps {
    propsMarkdown: string[];
}

interface FrontMatter {
    title?: string;
    image?: string;
    description?: string;
    // 추가적인 속성이 있다면 여기에 추가
}

const Main: React.FC<WorkProps> = ({ propsMarkdown }) => {
    // remark 설정
    const processor = unified()
        .use(remarkParse)
        .use(remarkFrontmatter)
        .use(remarkHtml); // 추가

    // front matter 추출한 전체 데이터 반환
    const allFrontMatter: FrontMatter[] = propsMarkdown.map((markdown) => {
        const result = processor.processSync(markdown);
        const frontMatter = yaml.load(result.data as unknown as string) as FrontMatter;
        return frontMatter;
    });

    return (
        <div className="main">
            <section className="post-feed">
                {allFrontMatter.map((frontMatter, index) => (
                    <a href="#." className="post-feed__list" key={index}>
                        <header
                            className="post-feed__list--image" 
                            style={{ backgroundImage: `url(${frontMatter.image || ''})` }}
                        ></header>

                        <section
                            className="post-feed__list--title"
                        >{frontMatter.title}</section>

                        <footer 
                            className="post-feed__list--content"
                        >{frontMatter.description}</footer>
                    </a>
                ))}
            </section>
        </div>
    );
};

export default Main;